import { Link } from "react-router-dom"

function NotFound() {
    return (
        <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
            <div className="text-center">
                <p className="text-base font-semibold text-coffee-600">404</p>
                <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">找不到頁面</h1>
                <p className="mt-6 text-base leading-7 text-gray-600">在為你準備的路上迷路了，請點選按鈕前往登入頁面。</p>
                <div className="mt-10 flex items-center justify-center gap-x-6">
                    <Link to='/login'
                        className="rounded-md bg-coffee-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-coffee-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-coffee-600"
                    >前往登入頁面</Link>
                    {/* <a href="#" className="text-sm font-semibold text-gray-900">
                        Contact support <span aria-hidden="true">&rarr;</span>
                    </a> */}
                </div>
            </div>
        </main>
    )
}
export default NotFound